html{
  box-sizing: border-box;
}
  
*,
*::after,
*::before {
  box-sizing: inherit;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
 }


button {
  padding: 0;
  border: none;
}

 ul {
  list-style: none;
 }
.container {
  max-width: 1240px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
 }

 body {
  /* box-sizing: border-box; */
  font-size: 20px;
  font-weight: 300;
  line-height: 1.5;
  font-family: "Open Sans", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
  font-variation-settings: "wdth" 100;
    /* font-family: 'Cormorant', serif; */
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 140px;
  padding: 30px 0;
}

.logo {
  margin-bottom: 35px;
}

.menu__btn {
  display: none;
  width: 40px;
  height: 20px;
  flex-direction: column;
  justify-content: space-between;
}

.menu__btn span {
  background-color: black;
  height: 2px;
  width: 100%;
}

.menu__list {
  display: flex;
  align-items: center;
  gap: 0 65px;
  font-weight: 400;
  font-size: 18px;
  margin-left: 85px;
}

.user-actions {
  display: flex;
  align-items: center;
  gap: 0 20px;
}

.user-actions__item__search {
  margin-left: 80px;
}

.top {
  background-image: url(../public/images/Group\ 91.jpg);
  background-repeat: no-repeat;
  /* background-position: bottom center; */
  background-size: cover;

  width: 100vw; /* Устанавливает ширину на 100% от ширины окна */
  position: relative;
}

.top__inner {
  min-height: 500px;
  color: #fff;
  font-family: 'Cormorant', serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  -webkit-text-stroke: 1px rgb(0, 0, 0);
}

.top__title {
font-size: 60px;
margin-bottom: 20px;
max-width: 780px;
line-height: 1;
font-weight: 400;
}

.top__text {
font-size: 24px;
margin-bottom: 48px;
max-width: 560px;
line-height: normal;
}

.Blockquote__text {
  text-align: center;
  font-family: 'Cormorant', serif;
  font-style: italic;
  font-size: 40px;
  font-weight: 300;
  line-height: normal;
  max-width: 704px;
  margin: 150px auto;
}


@media (max-width: 1000px) {
  .user-actions__item__search {
    margin-left: 30px;
  }
  .menu {
    margin-right: auto;
  }
  .menu__list {
    margin-left: 60px;
  }
  .logo {
    margin-bottom: 0;
  }
  .header__inner {
    min-height: auto;
  }
  .Blockquote__text {
    margin-top: 120px;
    margin-bottom: 135px;
  }
}

@media (max-width: 860px) {
.menu__list{
  display: none;
}
.menu__btn{
  display: flex;
}
.menu {
  order: -1;
  margin-right: 0;
}
.logo {
  margin-left: auto;
  margin-right: 100px;
}
}


@media (max-width: 768px) {
  .header__inner {
    padding-left: 64px;
    padding-right: 64px;
  }

  .Blockquote__text {
    max-width: 640px;
  }
}

@media (max-width: 480px) {}

@media (max-width: 360px) {}
